import jsonp from 'common/js/jsonp'
import {commonParams, options} from './config'
import axios from 'axios'

export function getHotKey() {
  const url = 'https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg'

  const data = Object.assign({}, commonParams, {
    uin: 0,
    needNewCode: 1,
    platform: 'h5'
  })

  return jsonp(url, data, options)
}

export function search2(query, page, zhida, perpage) {
  const url = 'https://c.y.qq.com/soso/fcgi-bin/search_for_qq_cp'

  const data = Object.assign({}, commonParams, {
    // 搜索字段
    w: query,
    // 搜索结果页数
    p: page,
    perpage,
    n: perpage,
    // 是否直达
    catZhida: zhida ? 1 : 0,
    zhidaqu: 1,
    t: 0,
    flag: 1,
    ie: 'utf-8',
    sem: 1,
    aggr: 0,
    remoteplace: 'txt.mqq.all',
    uin: 0,
    needNewCode: 1,
    platform: 'h5'
  })

  return jsonp(url, data, options)
}

// 搜索结果数据
export function search(query, page, zhida, perpage) {
  // 通过自身模拟服务器访问有host权限的数据
  const urlfront = '/api/getSearch'
  // 把hash做成对象,es6创建对象并赋值,
  // 固定的hash写在变量js模块里， config.js
  const thehash = Object.assign({}, commonParams, {
    // 关键词
    w: query,
    // 展示页数
    p: page,
    // 一页展示多少数据
    perpage: perpage,
    n: perpage,
    // 是否单独展示歌手
    catZhida: zhida ? 1 : 0,
    notice: 0,
    platform: 'h5',
    needNewCode: 1,
    zhidaqu: 1,
    t: 0,
    flag: 1,
    ie: 'utf-8',
    sem: 1,
    aggr: 0,
    remoteplace: 'txt.mqq.all',
    format: 'json'
  })
  // 执行jsonp模块
  return axios.get(urlfront, {
    params: thehash
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}



